<template>
  <div class="report-detail" v-if="loaded&&!msg">
    <h5>{{title}}</h5>
    <div class="block" v-for="(item, index) in content">
      <blockquote>
        <p><strong>{{index+1}}、{{item.item_title}} {{item.item_type}} {{item.item_progress}}:</strong></p>
        <p><small><strong>今日重要进展：</strong><span v-html="changeHtml(item.item_content)"></span></small></p>
        <p style="color: #3498DB"><small><strong>集成/联调进度：</strong><span>{{item.item_together}}</span></small></p>
        <p style="color: #E74C3C"><small><strong>风险点：</strong><span v-html="changeHtml(item.item_rist)"></span></small></p>
        <p v-if="item.item_remark" style="color: #BDC3C7"><small><strong>备注：</strong><span v-html="changeHtml(item.item_remark)"></span></small></p>
      </blockquote>
    </div>
    <div class="btn-group pull-right">
      <a class="btn btn-primary btn-lg" @click="gotoEdit()">编辑</a>
      <a class="btn btn-danger btn-lg" @click="deleteItem()">删除</a>
    </div>
  </div>
  <div v-else>
    {{msg}}
  </div>
</template>

<script>

import ReportCoreContent from '@/components/reportCore.vue';
import Base from './base.js';

export default {
  name: 'reportIndex',
  mixins: [Base],
  data () {
    return {
      content: [],
      title: '',
      loaded: false,
      msg: '加载中，请稍后...'
    }
  },
  components:{
    ReportCoreContent
  },
  mounted(){
    this.getInfo(this.$route.params.id);
  },
  methods: {
    getInfo(id){
      var that = this;
      $.ajax({
        url: '/dayreport/api/getreportbyid',
        data: {
          token: that.token,
          id: id
        },
        type: 'POST',
        dataType: 'json',
        success: function(res) {
          that.loaded = true;
          if(res.code != 200){
            that.msg = res.msg;
          }else{
            that.msg = '';
            that.title = res.data.info.title;
            that.content = JSON.parse(res.data.info.content);
          }
        }
      });
    },
    changeHtml(res){
      var reg3 = new RegExp("\n", "g");
      if(res){
        res = res.replace(reg3, "<br/>");
      }
      return res;
    },
    deleteItem() {
      var that = this;
      $.ajax({
        url: '/dayreport/api/deletereport',
        data: {
          token: that.token,
          id: that.$route.params.id
        },
        type: 'POST',
        dataType: 'json',
        success: function(res) {
          if(res.code != 200){
            that.$wii.toast.show({
              text: res.msg,
              type: 'warn',
              time: 1500
            });
          }else{
            that.$wii.toast.show({
              text: '删除成功！',
              type: 'success',
              time: 1500
            });
            setTimeout(() => {
              that.$router.push({name: 'index'})
            },1000);
          }
        }
      })
    },
    gotoEdit(){
      var that = this;
      that.$router.push({name: 'edit', params: {id: that.$route.params.id}})
    }
  }
}
</script>
<style lang="less" scoped>
  .report-detail{
    p {
      span{
        display: block;
        padding-left: 20px;
        font-size: 16px;
      }
    }
  }
</style>



