<template>
  <div class="report-core-content">
    <div class="form-group col-sm-3 col-xs-6">
      <select class="form-control login-field" placeholder="模块名称" :value="content.item_title" name="item_title">
        <option value="">请选择</option>
        <template v-for="item in modelList">
          <option v-for="opt in item.list" :value="item.type+'-'+opt">{{item.type+'-'+opt}}</option>
        </template>
      </select>
      <label class="login-field-icon fui-tag">&nbsp;产品&nbsp;</label>
    </div>

    <div class="form-group col-sm-3 col-xs-6">
      <select :value="content.item_type" class="form-control login-field" name="item_type">
        <option value="">请选择</option>
        <option value="H5">H5</option>
        <option value="后端">后端</option>
        <option value="客户端">客户端</option>
      </select>
      <label class="login-field-icon fui-windows">&nbsp;类型&nbsp;</label>
    </div>

    <div class="form-group col-sm-3 col-xs-6">
      <input type="text" class="form-control login-field" placeholder="进度(如:50%)" :value="content.item_progress" name="item_progress"/>
      <label class="login-field-icon fui-loop">&nbsp;进度&nbsp;</label>
    </div>

    <div class="form-group col-sm-3 col-xs-6">
      <input type="text" class="form-control login-field" placeholder="集成/联调(如:50%)" :value="content.item_together" name="item_together"/>
      <label class="login-field-icon fui-cmd">&nbsp;联调&nbsp;</label>
    </div>

    <div class="form-group col-sm-6 col-xs-12">
      <textarea class="form-control login-field" placeholder="今日重要进展" :value="content.item_content" name="item_content"></textarea>
    </div>

    <div class="form-group col-sm-6 col-xs-12">
      <textarea class="form-control login-field" placeholder="风险" :value="content.item_rist" name="item_rist"></textarea>
    </div>

    <div class="form-group col-sm-12 col-xs-12">
      <textarea class="form-control login-field" placeholder="备注" :value="content.item_remark" name="item_remark"></textarea>
    </div>
    <div class="form-group col-sm-12 col-xs-12">
      <div class="btn btn-lg btn-danger" @click="deleteOne">删除</div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'ReportCoreContent',
  props: {
    content: {
      type: Object,
      default: function(){
        return {
          item_title: '',
          item_type: '',
          item_progress: '',
          item_rist: '',
          item_content: '',
          item_remark: '',
          item_together: ''
        }
      }
    }
  },
  data () {
    return {
      modelList: [{
        type: 'APP',
        list: ['机票', '酒店', '餐饮', '门票', '城市名片', '景区名片', '天气', '直播', '语音导览', '投诉', '找厕所', '购', '找导游/旅行社', '首页/聚合页面', '个人中心', '订单中心']
      }, {
        type: '小程序',
        list: ['找厕所', '门票', '诚信商户/支付', '直播']
      }, {
        type: '其它',
        list: ['织云系统', 'Mock系统', 'WAU', 'WMU', '其它']
      }]
    }
  },
  mounted(){
    // this.content.item_progress = 1;
  },
  components:{
   
  },
  methods: {
    deleteOne(){
      this.$emit('on-delete-one');
    }
  }
}
</script>
<style lang="less">
  .report-core-content{
    margin-top: 10px;
    padding-top: 10px;
    select{
      appearance:none;
      -moz-appearance:none;
      -webkit-appearance:none;
    }
    textarea{
      resize: none;
      width: 100%;
      height: 100px;
      padding: 5px;
    }
  }
</style>



